	module.exports = {"data":{"markdownRemark":{"html":"<p>Every HTML elements has a parent element from which it inherits properties.</p>\n<p>For example, your jQuery Playground <code>h3</code> element has the parent element of<code>&#x3C;div class=\"container-fluid\"></code>, which itself has the parent body.</p>\n<p>jQuery has a function called <code>parent()</code> that allows you to access the parent of whichever element you’ve selected.</p>\n<p>Here’s an example of how you would use the <code>parent()</code> function:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$(\"#left-well\").parent().css(\"background-color\", \"blue\")</code></pre>\n      </div>","fields":{"slug":"/certificates/target-the-parent-of-an-element-using-jquery/"},"frontmatter":{"title":"Target the Parent of an Element Using jQuery"}}},"pathContext":{"slug":"/certificates/target-the-parent-of-an-element-using-jquery/"}}

